.sv_progress-buttons__container
  .sv_progress-buttons__image-button-left.sv_progress-buttons__image-button--hidden,
.sv_progress-buttons__container
  .sv_progress-buttons__image-button-right.sv_progress-buttons__image-button--hidden {
  display: none; /* left and right arrow */
}

.sv_progress-buttons__container .sv_progress-buttons__list-container {
  width: 100%;
}

.sv_progress-buttons__container .sv_progress-buttons__list {
  padding: 0;
  margin: 0;
  width: 90%;
  max-width: 620px;
}

.sv_progress-buttons__container .sv_progress-buttons__list li {
  width: 20px;
  margin: 0 3.5%;
  cursor: pointer;
}

.sv_progress-buttons__container .sv_progress-buttons__list li::before {
  border: 0;
  border-top: 4px solid transparent;
  border-bottom: 4px solid transparent;
  border-radius: 0;
  background-color: transparent;
  width: 20px;
  height: 24px;
  padding: 6px 0;
  margin: 0 auto;
  transition: all 0.2s ease-in-out;
}

.sv_progress-buttons__container .sv_progress-buttons__list li::after {
  display: none;
}

.sv_progress-buttons__container
  .sv_progress-buttons__list
  li:hover
  .sv_progress-buttons__page-title {
  color: var(--black);
}

.sv_progress-buttons__container
  .sv_progress-buttons__list
  li.sv_progress-buttons__list-element--current::before {
  border-top-color: var(--black);
  border-bottom-color: var(--black);
}

.sv_progress-buttons__container
  .sv_progress-buttons__list
  li.sv_progress-buttons__list-element--current
  .sv_progress-buttons__page-title {
  color: var(--black);
}

.sv_progress-buttons__container
  .sv_progress-buttons__list
  li.sv_progress-buttons__list-element--passed.sv_progress-buttons__list-element--current::before {
  border-top-color: var(--black);
  border-bottom-color: var(--black);
}

.sv_progress-buttons__container
  .sv_progress-buttons__list
  li.sv_progress-buttons__list-element--passed::before {
  border-color: transparent;
  background-color: transparent;
}

.sv_progress-buttons__container
  .sv_progress-buttons__list
  li
  .sv_progress-buttons__page-title {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: var(--light-grey);
  font-family: var(--font-family-gilroySemi);
  font-size: 19px;
  font-weight: unset;
}

.sv_progress-buttons__container
  .sv_progress-buttons__list
  li
  .sv_progress-buttons__page-description {
  display: none;
}
