.sv-root-modern .sv-footer .sv-btn {
  display: inline-block;
  padding: 12px 0;
  color: var(--black);
  border: 2px solid var(--black);
  font-family: var(--font-family-gilroySemi);
  font-size: 14px;
  text-align: center;
  font-weight: normal;
  padding: 12px 36px;
  line-height: 1.4;
}

.sv-root-modern .sv-footer .sv-btn.sv-footer__prev-btn,
.sv-root-modern .sv-footer .sv-footer__save-exit-btn {
  background-color: transparent;
  color: var(--black);
}

.sv-root-modern .sv-footer .sv-btn.sv-footer__prev-btn:hover,
.sv-root-modern .sv-footer .sv-footer__save-exit-btn:hover {
  background-color: var(--black);
  color: var(--white);
}

.sv-root-modern .sv-footer .sv-footer__next-btn {
  border: 2px solid var(--primary);
  color: var(--white);
  background-color: var(--primary);
  background-image: url('/img/arrow_w.svg');
  background-repeat: no-repeat;
  background-size: 12px;
  background-position: right 24px center;
  padding-left: 36px;
  padding-right: 46px;
}

.sv-root-modern .sv-footer .sv-footer__next-btn:hover,
.sv-root-modern .sv-footer .sv-footer__complete-btn:hover {
  background-color: var(--black);
  border: 2px solid var(--black);
  color: var(--white);
}

.sv-root-modern .sv-footer .sv-footer__complete-btn {
  background-color: var(--primary);
  border: 2px solid var(--primary);
  color: var(--white);
}
.sv-footer__save-exit-btn {
  order: 2;
  margin-right: 5px;
}
.sv-footer__prev-btn {
  order: 3;
  /* surveyjs generates button with inline style */
  margin-right: auto !important;
}
