/* global */
.sv-body__page {
  margin: 0px 0px 60px 0px;
}

.sv-root-modern {
  margin: 0 auto;
  text-align: left;
}

.sv-root-modern a {
  color: var(--primary);
}

/* completedpage */
body .sv-root-modern .sv-completedpage {
  background-color: var(--white);
  display: flex;
  text-align: center;
  flex-direction: column;
  font-family: var(--font-family-avenirmedium);
  font-weight: 500;
  padding-top: 0;
  padding-bottom: 0;
  height: 90%;
}

.sv-completedpage:before {
  content: unset;
}

.sv-completedpage-text {
  margin-bottom: 78px;
}

.sv-completedpage-btn {
  margin: auto;
  padding: 0 10px;
}

.sv-completedpage-btn a {
  color: var(--white);
}

.sv-completedpage-btn a:hover {
  color: var(--white);
}

/* elements */
.sv-header__text {
  text-align: center;
}
.sv-header__text span {
  font-family: var(--font-family-bigCaslon);
  font-size: 44px;
  font-weight: 400;
  color: var(--black);
  margin-top: 0px;
  margin-bottom: 30px;
}

/* main title */
.sv-root-modern .sv-container-modern__title {
  text-align: center;
  padding-top: 0;
  padding-bottom: 0;
  margin-bottom: 30px;
}

.sv-root-modern .sv-container-modern__title h3 {
  font-family: var(--font-family-bigCaslon);
  font-size: 44px;
  font-weight: 400;
  line-height: 1.4;
}

.sv-page__title {
  font-family: var(--font-family-bigCaslon);
  font-size: 28px;
  font-weight: 400;
  color: var(--black);
  padding-left: 0;
  margin-top: 0px;
  margin-bottom: 20px;
}

.sv-root-modern .sv-page__title + .sv-page__description {
  color: var(--grey);
  min-height: 0;
  padding-left: 0;
  margin: 0 0 20px;
}

.sv-form-control {
  font-family: var(--font-family-avenirmedium);
  color: var(--black);
  font-size: 14px;
  font-style: normal;
  font-variant: normal;
  letter-spacing: normal;
  margin-bottom: 20px;
}
.sv-title-text {
  font-weight: normal;
  font-size: 14px;
  width: 100%;
  margin-bottom: 15px;
  color: var(--black);
  margin: 0 0 15px;
}
.sv-selectbase__item {
  margin-bottom: 1em;
  vertical-align: middle;
}
.sv-selectbase__item .sv-selectbase__label {
  display: flex;
  align-items: center;
}
h5 {
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0;
  margin-inline-end: 0;
}
.sv-panel .sv-row:not(:last-child) {
  padding-bottom: 0;
  margin-bottom: 0;
}
.sv-row:not(:last-child) {
  padding-bottom: 0;
}
.sv-panel__title {
  color: white;
  font-family: var(--font-family-gilroySemi);
  font-size: 16px;
  text-indent: 10px;
  padding-top: 13px;
  padding-bottom: 13px;
  background: var(--primary);
}
.sv-item__control-label {
  padding-top: 0;
  width: 100%;
}
body .sv-root-modern ::-webkit-scrollbar-thumb {
  background: var(--primary);
}
.sv-question__required-text {
  font-size: 1em;
  color: red;
}
.sv-table__cell {
  min-width: 18.5em;
}
.sv-table .sv-radio-group-wrapper .sv-radio-group-item,
.sv-table .sv-checkbox-wrapper .sv-checkbox-group-item {
  flex-basis: unset;
  margin-bottom: 0.8em;
}
.header-cell {
  text-align: center;
}
.sv-question {
  overflow: hidden;
}
.sv-question__content {
  font-size: 14px;
  font-family: var(--font-family-avenirmedium);
  margin-left: 0;
}
.sv-form-control .sv-question__description {
  margin: 0 0 15px;
  padding-left: unset;
  font-size: 14px;
  word-break: break-word;
}
.sv-root-modern .sv-form-control .sv-question__description {
  color: var(--grey);
}
.sv-root-modern .sv-progress .sv-progress__bar {
  background-color: var(--primary);
}

.sv-row {
  flex-wrap: unset;
}

@media screen and (max-width: 768px) {
  .sv-row {
    flex-wrap: wrap;
  }
}

.sv-root-modern .sv-body__footer {
  margin: 0 auto 60px;
  padding: 0;
  width: auto;
  display: flex;
  flex-direction: row-reverse;
}
.sv-root-modern .sv-footer .sv-btn {
  display: inline-block;
  padding: 12px 0;
  color: var(--black);
  border: 2px solid var(--black);
  font-family: var(--font-family-gilroySemi);
  font-size: 14px;
  text-align: center;
  font-weight: normal;
  padding: 12px 36px;
}

/* Buttons */
.sv-root-modern .sv-btn {
  transition: all 0.2s ease-in-out;
  border-radius: 0;
}

.sv-form-control .sv-file-root .sv-file-btn,
.sv-form-control .sv-paneldynamic-root .sv-paneldynamic-btn {
  dispay: inline-block;
  padding: 12px 0;
  color: var(--black);
  border: 2px solid var(--black);
  font-family: var(--font-family-gilroySemi);
  font-size: 14px;
  text-align: center;
  font-weight: normal;
  padding: 12px 36px;
}

.sv-form-control .sv-file-root .sv-file-btn,
.sv-form-control .sv-paneldynamic-root .sv-paneldynamic-btn {
  background-color: transparent;
}

.sv-form-control .sv-file-root .sv-file-btn:hover,
.sv-form-control .sv-paneldynamic-root .sv-paneldynamic-btn:hover {
  background-color: var(--black);
  border: 2px solid var(--black);
  color: var(--white);
}

.sv-form-control .sv-file-root .sv-btn-primary,
.sv-form-control .sv-paneldynamic-root .sv-btn-primary {
  background-color: var(--primary);
  border: 2px solid var(--primary);
  color: var(--white);
}

/* 3rd party widgets */
.noUi-base .noUi-connect {
  background: var(--primary);
}
.noUi-value {
  margin-top: 6px;
  font-size: 14px;
}
.noUi-horizontal {
  margin: 0 30px;
}
.noUi-tooltip {
  font-size: 14px;
}

/* progress bar */
.sv_progress-buttons__container-center {
  position: sticky;
  top: 62px;
  background-color: var(--white);
  padding-top: 26px;
  padding-bottom: 30px;
  z-index: var(--z-index-highest);
}

/* text input */
.sv-text {
  border: 1px solid #aaa;
}

.sv-form-control .sv-file-root .sv-btn-primary.sv-file__choose-btn--disabled {
  background-color: var(--grey);
  border-color: var(--grey);
  opacity: 0.5;
  pointer-events: none;
}
.sv-multipletext .sv-multipletext-field__cell > div {
  margin-right: 30px;
}
